/* ==========================================================================
   Index
   ========================================================================== */

.container {
  @apply relative;
}

.container .post .post-box {
  @apply pb-calc-4rem-2vw pt-calc-4rem-2vw;
}

.container .post .post-box .post-title {
  @apply text-gray-800;
}

#index .post-title {
  @apply text-5xl;
}

.container .post.featured .post-title:after {
  @apply absolute w-16 h-16 -left-16 top-0 font-normal not-italic text-center text-0_5em font-icon text-yellow-400 content-title-after;
}

@media (min-width: 640px) {
    .tag-template .container {
      @apply max-w-full;
  }
}

#index h2 a:hover {
  @apply underline;
}